$font_path: '../fonts';

@font-face {
	font-family: Lato;
	font-style: normal;
	font-weight: 300;
	src: url('#{$font_path}/lato-v14-latin-300.eot');
	src: local("Lato Light"), 
	local("Lato-Light"), 
	url('#{$font_path}/lato-v14-latin-300.eot?#iefix') format("embedded-opentype"), 
	url('#{$font_path}/lato-v14-latin-300.woff2') format("woff2"), 
	url('#{$font_path}/lato-v14-latin-300.woff') format("woff"), 
	url('#{$font_path}/lato-v14-latin-300.ttf') format("truetype"), 
	url('#{$font_path}/lato-v14-latin-300.svg#Lato') format("svg");
}

@font-face {
	font-family: Lato;
	font-style: italic;
	font-weight: 300;
	src: url('#{$font_path}/lato-v14-latin-300italic.eot');
	src: local("Lato Light Italic"), 
	local("Lato-LightItalic"), 
	url('#{$font_path}/lato-v14-latin-300italic.eot?#iefix') format("embedded-opentype"), 
	url('#{$font_path}/lato-v14-latin-300italic.woff2') format("woff2"), 
	url('#{$font_path}/lato-v14-latin-300italic.woff') format("woff"), 
